// social-media-links styles
#block-bean-social-media-links {
    a {
        @include transition((border-color, color), $animTime, $easeOutExpo);

        border: 1px solid $grey4;
        border-radius: 50%;
        display: block;
        font-size: 0;
        height: 40;
        padding: 10px;
        text-align: center;
        text-decoration: none;
        width: 40;

        &:before {
            font-size: 16px;
        }

        &:hover,
        &:focus {
            color: $orange1;
            border-color: $orange1;
        }
    }

    .field-type-link-field {
        display: inline-block;
        margin: 0 4px;
    }

    .field-name-field-facebook {
        a {
            &:before {
                @extend %icomoon;

                content: $icon-facebook;
            }
        }
    }

    .field-name-field-linkedin {
        a {
            &:before {
                @extend %icomoon;

                content: $icon-linkedin;
            }
        }
    }

    .field-name-field-twitter {
        a {
            &:before {
                @extend %icomoon;

                content: $icon-twitter;
            }
        }
    }

    #footer & {
        float: right;
        margin-top: 18px;
    }
}
