// course-listing styles
.page-open-learning {
    .page__title {
        margin: 50px 0;
        text-decoration: underline;
    }

    .view-view-course-listing {
        .view-content {
            @extend .clearfix;

            margin: 0 -10px;

            img {
                height: auto;
                width: 100%;
            }

            .views-row {
                min-height: 300px;
                margin-bottom: 30px;
                padding: 0 10px;

                @include tablet {
                    float: left;
                    width: 33.333333%;
                }
            }

            .views-field,
            .field-content {
                min-height: 300px;
            }
        }
    }

    .course {
        min-height: 300px;
        overflow: hidden;

        img {
            @extend .sr-only;
        }

        &__item,
        &__wrap {
            min-height: 300px;
        }

        &__wrap {
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            position: relative;
        }

        &__time {
            @include font(13);

            background-color: #E8E8E8;
            border-radius: 20px;
            color: #222222;
            display: inline-block;
            padding: 10px 15px;
            position: relative;

            &:before {
                @extend %icomoon;

                content: $icon-clock;
                color: $orange1;
                display: inline-block;
                font-size: 17px;
                height: 17px;
                margin-right: 20px;
                width: 17px;
                vertical-align: bottom;
            }

            &:after {
                background-color: #BABABA;
                bottom: 0;
                content: '';
                display: block;
                height: 20px;
                margin: auto;
                position: absolute;
                left: 40px;
                top: 0;
                width: 2px;
            }
        }

        &__detail {
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
            bottom: 0;
            color: #FFFFFF;
            padding: 20px;
            position: absolute;
            width: 100%;
        }

        &__title {
            @include font(24);

            color: #FFFFFF;
            display: block;
            font-family: "Crimson Text";
            font-weight: bold;

            &:hover,
            &:focus {
                color: #fff;
                text-decoration: none;
            }
        }

        &__desc {
            @include font(13);

            display: none;
        }

        &__summary {
            margin-bottom: 30px;
        }

        &__controls {
            border-top: 1px solid #DEDEDE;
            margin-top: 10px;
            padding: 10px 0 0;

            ul {
                @include normalise-list;

                display: flex;
                justify-content: space-between;
            }

            li {
                margin-right: 10px;

                &:last-child,
                &:only-child {
                    margin-right: 0;
                }
            }

            button {
                text-decoration: underline;
            }

            a {
                @include transition(color, $animTime, $easeOutExpo);

                color: #fff;
                display: inline-block;
                vertical-align: middle;
                text-decoration: none;

                &:before {
                    @extend %icomoon;

                    content: $icon-play-button;
                    display: inline-block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 10px;
                    position: relative;
                    top: 4px;
                    width: 20px;
                }

                &:hover,
                &:focus {
                    color: $orange2;
                }
            }
        }

        &__preview {
            display: none;
        }
    }
}
