#block-menu-menu-homepage-defence-navigation {
    background: $background1;
    display: none;
    margin: 0 -20px;

    @include tablet {
        display: block;
    }


    .block__content {
        height: 100%;
        margin: auto;
        max-width: 1440px;
        padding: 0 $gutter;
        position: relative;
        width: 100%;
        z-index: 1;

        @include large-desktop {
            max-width: 1160px;
        }


        ul {
            @include normalise-list;

            text-align: right;

            li {
                display: inline-block;

                a {
                    @include font(11);

                    color: $white;
                    display: block;
                    padding: 15px;
                    text-decoration: none;

                    &:hover,
                    &:focus {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}
