// search styles
#page {
    position: relative;
    z-index: 5;
}

#header {
    &.-active {
        .container {
            #block-views-exp-core-areas-of-study-page-1 {
                left: -20px;
                right: -20px;
                width: 103.5%;
                height: 100%;

                @include mobile {
                    left: 0px;
                    right: 0px;
                    width: 100%;
                    height: 100%;
                }


                @include tablet {
                    left: 0px;
                    right: 0px;
                    width: 100%;
                    height: 100%;
                }


                .block__content {
                    @include mobile {
                        width: 100%;
                        height: 100%;
                    }


                    @include large-desktop {
                        margin-top: 65px !important;
                        padding-left: 0px !important;
                    }
                }

                .search__title {
                    left: 0;;
                }
            }
        }
    }
}

#block-views-exp-core-areas-of-study-page-1 {
    #header & {
        height: 100%;
        right: 0;
        position: absolute;
        display: inline-block;
        top: 0;
        z-index: 5;
        width: 20%;

        @include large-desktop {
            width: 20%;
            height: 100%;
        }


        .block__content {
            margin-top: 5px;

            @include mobile {
                margin-top: -10px;
            }


            @include tablet {
                margin-top: -10px;
            }


            @include desktop {
                margin-top: 5px;
            }


            @include large-desktop {
                margin-top: 77px !important;
                height: 70px;
                padding: 15px 0px;
                box-sizing: border-box;
            }
        }

        .search-form-widget {
            & > div {
                position: relative;
            }
        }

        .form-item {
            margin-top: 100px;
            width: 100%;

            @include large-desktop {
                margin-top: 0;
            }


            .form-text {
                @include font(28);

                background-color: transparent;
                border-radius: 0;
                border: 0;
                color: $white;
                font-family: 'Crimson Text', serif;
                font-weight: $bold;
                height: 100%;
                padding: 5px 40px;
                padding-right: 70px;
                width: 100%;

                // .not-front & {
                //     border-bottom: 1px solid $yellow;

                //     @include large-desktop {
                //         border-bottom: 0;
                //     }
                // }

            }
        }

        .form-submit {
            background: url(../images/svg/search-white.svg) no-repeat center;
            background-size: 20px;
            border: 0;
            color: $white;
            font-size: 0;
            height: 40px;
            position: absolute;
            display: inline-block;
            right: 0;

            // top: 5px;
            border: 1px solid $white;
            width: 40px;

            @include large-desktop {
                background: rgba(0, 0, 0, 0.5) url(../images/svg/search-white.svg) no-repeat 85px center;
                background-size: 20px;
                right: 20px;

                // top: 85px;
                width: 120px;
            }


            &:hover {
                @include large-desktop {
                    background: $white url(../images/svg/search.svg) no-repeat 85px center;
                    background-size: 20px;
                }
            }

            &.-active {
                @include large-desktop {
                    background: $white url(../images/svg/search.svg) no-repeat center;
                    background-size: 20px;
                    padding: 0;
                    width: 51px;
                    position: absolute;
                    display: block;
                    bottom: 0px;
                    margin-right: -20px;
                    margin-bottom: 26px !important;
                }


                @include tablet {
                    background: $white url(../images/svg/search.svg) no-repeat center;
                    background-size: 20px;
                    padding: 0;
                    width: 51px;
                    position: absolute;
                    display: block;
                    bottom: 0px;
                    margin-bottom: -88px;
                }


                @include mobile {
                    background: $white url(../images/svg/search.svg) no-repeat center;
                    background-size: 20px;
                    padding: 0;
                    width: 51px;
                    position: absolute;
                    display: block;
                    bottom: 0px;

                    // margin-bottom: 15px;
                    margin-bottom: -99px;
                }
            }
        }

        .views-exposed-widget {
            float: none;

            @include desktop {
                padding: 0 20px;
            }


            @include tablet {
                padding-right: 0px !important;
                padding-left: 0px !important;
            }


            @include mobile {
                padding-right: 0px;
            }
        }

        .search__title {
            @include font(32);

            color: $white;
            font-family: 'Crimson Text', serif;
            font-weight: $bold;
            line-height: 1l;
            position: absolute;
            left: -260px;
            top: 50px;

            @include large-desktop {
                // left: 20px;
                top: 20px;
            }
        }

        .search__close {
            @include font(16);

            color: $white;
            padding-right: 35px;
            text-decoration: none;
            top: 0px;
            position: absolute;
            right: -10px;

            @include large-desktop {
                top: 26px !important;
                right: 20px;
            }


            @include tablet {
                top: 57px;
                right: 0px;
            }


            @include mobile {
                top: 15px;
                right: 0px;
            }


            &:before,
            &:after {
                background-color: $white;
                border-radius: 2px;
                content: '';
                display: block;
                height: 4px;
                position: absolute;
                right: 0;
                top: 10px;
                width: 24px;
            }

            &:before {
                transform: rotate(45deg);
            }

            &:after {
                transform: rotate(-45deg);
            }
        }
    }
}

#views-exposed-form-core-areas-of-study-page-1 {
    .views-exposed-widget {
        .form-type-textfield {
            #header & {
                display: none;
            }
        }
    }
}

.autocomplete-suggestions {
    @include font(18);

    color: $white;
    font-family: 'Crimson Text', serif;
    margin-top: 2px;
    overflow: hidden;
    padding-left: 40px;

    @include desktop {
        @include font(24);
    }


    &.-sidebar {
        @include font(18);

        background-color: $white;
        border: 1px solid #bab8b5;
        color: #000;
        margin-top: -3px;
        padding: 5px 20px;

        .autocomplete-suggestion {
            margin: 10px 0;

            strong {
                color: #000;
                font-weight: $bold;
            }
        }
    }

    .autocomplete-suggestion {
        margin: 15px 0;

        strong {
            color: #faed95;
            font-weight: $normal;
        }
    }
}
