// share styles
.share {
    @include font(14);

    color: $grey1;
    position: absolute;

    @media (max-width: 410px) {
        margin-bottom: 20px;
        position: relative;
    }

    #block-views-recently-forged-ideas-block &,
    #block-views-related-to-this-topic-block--2 & {
        top: 30px;
        right: 30px;

        @media (max-width: 410px) {
            top: 0;
            right: 0;
        }
    }

    #block-views-7e2c62155b1d71d8de0378743eddbe46 & {
        right: 0;

        @media (max-width: 410px) {
            right: 0;
        }
    }

    #header &,
    #block-views-details-view-block & {
        border-bottom: 1px solid #c0c0c0;
        color: $grey1;
        margin-bottom: 30px;
        padding-bottom: 30px;;
        position: static;
    }

    ul {
        display: none;
        border: 1px solid $grey1;
        border-radius: 30px;
        background-color: $white;
        position: absolute;
        padding: 11px 15px;
        right: 0;
        top: 0;
        width: 196px;

        @media (max-width: 410px) {
            right: auto;

            .view-core-areas-of-study & {
                right: 0;
            }
        }

        @include tablet {
            right: -10px;
        }


        #block-views-details-view-block & {
            left: 0;
            margin: auto;
            right: auto;
        }

        .view-standard-page-hero-image & {
            margin: auto;
            left: 0;
        }

        li {
            display: inline-block;
        }

        span {
            @extend .sr-only;
        }
    }

    button.share__link {
        height: 18px;
        position: relative;
        width: 18px;

        &:before,
        &:after {
            @include absolute-center;

            background-color: #000;
            content: '';
            display: block;
            height: 18px;
            position: absolute;
            transform: rotate(45deg);
            width: 2px !important;
        }

        &:hover{
          &:before,
          &:after {
              background-color: $orange1;
          }
        }

        &:after {
            transform: rotate(-45deg);
        }
    }

    &__link {
        @include transition(color, $animTime, $easeOutExpo);

        background-color: transparent;
        border: 0;
        display: inline-block;
        margin: 0 5px;
        padding: 0;
        text-decoration: none;
        vertical-align: top;

        &:before {
            @extend %icomoon;

            font-size: 18px;
            height: 18px;
            width: 18px;
        }

        &:hover,
        &:focus {
            color: $orange1;
        }

        &.-fb {
            &:before {
                content: "\e90e";
                display: block !important;
            }
        }

        &.-tw {
            &:before {
                content: "\e903";
                display: block !important;
            }
        }

        &.-lk {
            &:before {
                content: "\e902";
                display: block !important;
            }
        }

        &.-em {
            &:before {
                content: "\e911";
                display: block !important;
            }
        }
    }

    &__btn {
        @include transition((background-color, border, color), $animTime, $easeOutExpo);

        background-color: transparent;
        border: 1px solid $grey1;
        border-radius: 21px;
        padding: 10px 20px;

        &:before {
            @extend %icomoon;

            content: "\e901";
            display: inline-block;
            font-size: 20px;
            height: 20px;
            margin-right: 10px;
            vertical-align: top;
            width: 20px;
        }

        &:hover, &:focus {
            background-color: $orange1;
            color: $white;
            border: 1px solid $orange1;
        }
    }
}

.share-this-popover {
    z-index: 10;
}
