$image_folder: "../images" !default;
$font_folder: "../typography" !default;
$font_size: 15;

$font-main: "Open Sans", sans-serif;
$font-title: "Crimson Text", serif;

$box-shadow: 0 1px 29px 0 rgba(0, 0, 0, 0.16);

$easeOutExpo: cubic-bezier(0.19, 1, 0.22, 1);
$easeInOutExpo: cubic-bezier(1, 0, 0, 1);
$animTime: 0.5s;

$bpMaxMobile: 767px;
$bpMinTablet: 768px;
$bpMinDesktop: 980px;
$bpMaxDesktop: 1200px;

$thin: 100;
$light: 300;
$normal: 400;
$medium: 600;
$bold: 700;
$ultra-bold: 900;

$gutter: 20px;