// forms styles

// Generic form styling
// Hide the checkboxes and radios and use CSS to display nicer elements
// Using the drupal classes
.form-radio, .form-checkbox {
    position: absolute;
    opacity: 0;
}

label {
    // Hide the checkboxes and radios and use CSS to display nicer elements
    + input[type=checkbox],
    + input[type=radio] {
        @extend .form-radio;
    }
}

input {
    &[type=text],
    &[type=email],
    &[type=date],
    &[type=datetime-local],
    &[type=month],
    &[type=number],
    &[type=password],
    &[type=time],
    &[type=search],
    &[type=tel],
    &[type=url],
    &[type=week] {
        outline: none;
        border: 1px solid #bab8b5;
        border-radius: 5px;
        transition: all 0.3s;
        padding: 0 15px;
        line-height: 42px;
        height: 40px;

        // IE fix
        &:focus, &:active {
            border-color: $orange1;
            box-shadow: 0 0 3px $orange1;
        }
    }

    &[type=radio] {
        // Commented out the hiding part because we need to have the input
        // next to the label and not inside it (drupal way)
        /*position: absolute;*/
        /*opacity: 0;*/
        + label {
            cursor: pointer;
            position: relative;
            padding-left: 40px;
            line-height: 20px;

            &:before {
                @include transition(border-color, $animTime, $easeOutExpo);

                content: '';
                position: absolute;
                left: 0;
                border: 1px solid #bab8b5;
                border-radius: 5px;
                box-sizing: border-box;
                width: 20px;
                height: 20px;
                text-align: center;
                border-radius: 100%;
                font-size: 0.7em;
                top: 0;
            }

            &:after {
                content: '';
                position: absolute;
                left: 0;
                border: 4px solid $white;
                margin: 1px;
                width: 18px;
                height: 18px;
                text-align: center;
                box-sizing: border-box;
                border-radius: 100%;
                font-size: 0.7em;
                background: $orange1;
                transition: all 0.3s;
                transform: scale(0);
                opacity: 0;
                top: 0;
            }

            &:hover, &:focus {
                &:after {
                    opacity: 0.5;
                    transform: scale(0.5);
                }
            }
        }

        &:hover, &:focus, &:active {
            + label {
                &:after {
                    opacity: 0.5;
                    transform: scale(0.5);
                }

                &:before {
                    border-color: $orange1;
                }
            }
        }

        &:checked {
            + label {
                &:after {
                    opacity: 1;
                    transform: scale(1);
                }
            }
        }

        &[disabled] + label:after {
            color: grey;
        }
    }

    &[type=checkbox] {
        @extend input[type=radio];

        + label {
            &:before {
                border-radius: 0px;
            }

            // @include icon(right, $icon-tick2);

            &:after {
                color: $orange1;
                line-height: 22px;
                margin: 1px;
                border: 0px;
                font-size: 0.9em;
                width: 20px;
                background: $orange1;
            }

            &:hover, &:focus {
                &:after {
                    opacity: 0.5;
                    transform: scale(0.5);
                    border-radius: 0;
                }
            }
        }

        &:hover, &:focus, &:active {
            + label {
                &:after {
                    opacity: 0.5;
                    transform: scale(0.5);
                    border-radius: 0;
                }

                &:before {
                    border-color: $orange1;
                }
            }
        }

        &:checked {
            + label {
                &:after {
                    opacity: 1;
                    transform: scale(0.8);
                    border-radius: 0;
                }
            }
        }

        &[disabled] + label:after {
            color: grey;
        }
    }

    &[type=submit] {
        @include button(rounded_grey);
        background: $white url(../images/svg/right-arrow.svg) no-repeat 130px;
        background-size: 7%;
        color: $white;
        padding: 10px 34px 10px 15px;
        width: 160px;

        &:hover {
            background: url(../images/svg/right-arrow-hover-orange.svg) no-repeat 130px;
            background-size: 7%;
        }
    }
}

select {
    @extend input[type=text];

    height: 42px;

    &[multiple] {
        height: 90px;
        transition: all 0.3s;

        &:focus, &:active {
            height: 100px;
        }
    }
}

textarea {
    @extend input[type=text];

    padding: 13px 15px;
    line-height: 1.5em;
    min-height: 100px;

    &:focus, &:active {
        min-height: 130px;
        border-color: $orange1;
        box-shadow: 0 0 3px $orange1;
    }
}

.form-required {
    color: #D73704;
    font-weight: $bold;
    position: relative;

    &:after {
        position: absolute;
        top: -5px;
    }
}

.messages {
    border: 0;
    background-image: none;
    font-size: 14px;
    line-height: 19px;
    color: #10374B;
    margin: 20px auto;
    box-shadow: none;
    padding: 15px;
    max-width: 1305px;

    &--status {
        background-color: #E5F3E6;
        border-left: 6px solid #46964C;
    }

    &--error {
        background-color: #F6DCDC;
        border-left: 6px solid #D0021B;
    }
}

.field-name-field-webform-introduction {
    @include font(17);

    line-height: 1.75;
    margin: 0 auto 50px;
    max-width: 800px;
}

.webform-component--content-summary {
    textarea {
        resize: vertical;
    }
}

.webform-client-form {
    .form-textarea-wrapper {
        @include desktop {
            display: inline-block;
            width: 49%;
        }


        textarea {
            margin-bottom: 0 !important;
        }
    }
}

.page-user {
    #content {
        margin: 90px auto 0;

        @include desktop {
            max-width: 66.666667%;
        }
    }

    #user-login {
        label {
            display: inline-block;
            width: 100px;
        }
    }

    .form-item {
        margin-bottom: 20px;
    }

    .form-text {
        width: 100%;

        @include desktop {
            width: auto;
        }
    }

    .description {
        @include font(13);

        font-style: italic;

        @include desktop {
            margin-left: 110px;
            margin-top: 5px;
        }
    }

    .form-submit {
        background-position: 165px;
        color: #000;
        width: 200px;

        &:hover {
            background-position: 175px;
        }
    }
}

.tabs-primary {
    @include clearfix;

    margin-bottom: 20px;

    li {
        background-color: #e8e8e8;
        float: left;
        padding: 10px;

        &.is-active {
            background-color: #D73704;
            font-weight: $bold;

            a {
                color: $white;
            }
        }
    }
}

.select {
    border: 1px solid #bab8b5;
    border-radius: 5px;
    height: 42px;
    overflow: hidden;
    position: relative;

    &:after {
        @extend %icomoon;

        backface-visibility: visible;
        bottom: 0;
        content: $icon-chevron-down;
        font-size: 1em;
        color: $orange2;
        height: 1em;
        margin: auto;
        position: absolute;
        right: 10px;
        top: 0;
        width: 1em;
    }

    select {
        border: 0;
        height: 40px;
        width: 120% !important;
    }
}

.views-widget-sort-sort_bef_combine {
    label,
    select {
        display: inline-block;
        vertical-align: middle;
    }

    .select {
        display: inline-block;
        width: 180px;
        vertical-align: middle;
    }
}

.form-managed-file {
    .form-file,
    .form-submit {
        color: $grey1;
        margin-left: 25px;
    }
}