// course-description styles
.node-type-course-description-page {
    #main {
        display: flex;
        flex-direction: column;
        z-index: 45 !important;

        @include tablet {
            flex-direction: row;
        }
    }

    .file-audio {
        min-height: 240px;
        position: relative;

        audio {
            @include absolute-center;
        }
    }

    .view-course-description-page-hero-banner-view-block- {
        @extend .container;

        min-height: 270px;
        padding: 0;

        video {
            margin: auto;
            width: 100%;
        }

        img {
            @extend .sr-only;
        }
    }

    .view-course-description-page-details-view-block- {
        background-color: #FFFFFF;
        box-shadow: 0 1px 29px 0 rgba(0, 0, 0, 0.16);
        margin-top: -20px;
        margin-bottom: 30px;
        padding: 25px;

        .share {
            border-bottom: 1px solid #F5F5F5;
            padding-bottom: 15px;
            position: static;

            &__btn {
                display: block;
                font-size: 11px;
                margin: auto;
                max-width: 110px;
                width: 100%;
            }

            ul {
                right: 20px;
                top: 5px;
            }

            li {
                margin-bottom: 0 !important;
            }
        }

        .views-field {
            @include font(10);

            margin-bottom: 10px;

            &:only-child,
            &:last-child {
                margin-bottom: 0;
            }

            li {
                margin-bottom: 10px;

                &:only-child,
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        .views-label-field-total-time-full-course {
            display: block;
            font-family: "Crimson Text";
            font-size: 16px;
            margin-bottom: 15px;
        }

        .views-field-field-total-time-full-course {
            .field-content {
                align-items: center;
                display: flex;

                &:before {
                    @extend %icomoon;

                    content: $icon-video;
                    display: block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 5px;
                    width: 20px;
                }
            }
        }

        .views-field-total-articles {
            .field-content {
                align-items: center;
                display: flex;

                &:before {
                    @extend %icomoon;

                    content: $icon-document;
                    display: block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 5px;
                    width: 20px;
                }
            }
        }

        .views-field-field-lifetime-acess {
            .field-content {
                align-items: center;
                display: flex;

                &:before {
                    @extend %icomoon;

                    content: $icon-unlock;
                    display: block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 5px;
                    width: 20px;
                }
            }
        }

        .views-field-field-mobile-tv-access {
            .field-content {
                align-items: center;
                display: flex;

                &:before {
                    @extend %icomoon;

                    content: $icon-mobile;
                    display: block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 5px;
                    width: 20px;
                }
            }
        }

        .views-field-field-certificate-of-completion {
            .field-content {
                align-items: center;
                display: flex;

                &:before {
                    @extend %icomoon;

                    content: $icon-recognition;
                    display: block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 5px;
                    width: 20px;
                }
            }
        }

        .views-field-total-downloads {
            .field-content {
                align-items: center;
                display: flex;

                &:before {
                    @extend %icomoon;

                    content: $icon-download;
                    display: block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 5px;
                    width: 20px;
                }
            }
        }
    }

    .course-description {
        &__header {
            margin-bottom: 30px;
            width: 100%;

            h1 {
                @include font(30);

                color: $grey1;
                font-weight: $bold;
                margin: 50px 0 20px;
                text-decoration: underline;
            }

            .field-name-field-text {
                margin-bottom: 20px;
            }
        }

        &__controls {
            @include font(13);

            display: flex;
            align-items: center;
            justify-content: flex-end;
            margin-bottom: 20px;

            ul {
                @include normalise-list;
            }
        }

        &__lectures {
            margin-right: 50px;
        }

        &__time {
            background-color: #E8E8E8;
            border-radius: 20px;
            display: flex;
            margin-right: 20px;
            padding: 10px 15px;
            position: relative;

            &:before {
                @extend %icomoon;

                content: $icon-clock;
                color: $orange1;
                font-size: 17px;
                height: 17px;
                margin-right: 20px;
                width: 17px;
            }

            &:after {
                background-color: #BABABA;
                bottom: 0;
                content: '';
                display: block;
                height: 20px;
                margin: auto;
                position: absolute;
                left: 40px;
                top: 0;
                width: 2px;
            }
        }

        &__cta {
            background-color: transparent;
            border: 0;
            padding: 0;
            margin-right: 50px;
            text-decoration: underline;

            &:hover,
            &:focus {
                text-decoration: none;
            }
        }
    }

    .course_topics_main_heading {
        align-items: center;
        border-bottom: 1px solid #F5F5F5;
        display: flex;
        cursor: pointer;
        flex-wrap: wrap;
        flex: 0 0 50%;
        justify-content: space-between;
        padding: 20px;
        padding-left: 40px;
        position: relative;

        &:before,
        &:after {
            @include transition(transform, $animTime, $easeOutExpo);

            background-color: $grey1;
            bottom: 0;
            content: '';
            display: block;
            height: 10px;
            margin: auto;
            left: 20px;
            position: absolute;
            transform: none;
            top: 0;
            width: 2px;
        }

        &:after {
            transform: rotate(90deg);
        }

        &.-expanded {
            &:before,
            &:after {
                transform: rotate(270deg);
            }
        }

        .field-name-field-topic-description {
            display: none;

            @include tablet {
                display: block;
                flex-basis: 100%;
                margin-top: 20px;
            }
        }
    }

    .field-name-field-course-topics {
        & > .field-items {
            & > .field-item {
                background-color: #FFFFFF;
                box-shadow: 0 1px 29px 0 rgba(0, 0, 0, 0.16);
                margin-bottom: 20px;

                &:only-child,
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        .field-name-field-title {
            @include font(18);

            color: $grey1;
            font-family: 'Crimson Text', serif;
            font-weight: $bold;
            line-height: 1;
            position: relative;
            top: 3px;
            width: 90%;;
        }

        .field-name-field-total-time-taken {
            text-align: right;;
            width: 10%;
        }

        .sub_topics_resources {
            display: none;

            .field-items {
                & > .field-item {
                    margin-bottom: 0 !important;
                }
            }

            .content {
                display: flex;
                justify-content: space-between;
                padding: 20px;
            }
        }

        .field-name-field-sub-topic {
            a {
                @include font(15);

                color: $grey1;
                font-family: "Open Sans";
                line-height: 23px;
            }

            .field-items {
                & > .field-item {
                    margin-bottom: 20px;

                    &:only-child,
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
}
