#block-views-recently-forged-ideas-block,
#block-views-related-to-this-topic-block--2 {
    background: $background2;
    padding: 30px 0;

    .block-title {
        @include font(32);

        margin: 0 auto;
        text-align: center;
        padding: 0 0 30px 0;
    }

    .slick-list {
        padding: 0 20px !important;
        z-index: 10;

        @include desktop {
            padding: 0 246px !important;
        }
    }

    .slick-slide {
        width: 650px;
        padding: 0 20px;

        img {
            height: auto;
            width: 100%;
        }
    }

    .slick-arrow {
        background-color: #d73704;
        height: auto;
        padding: 14px;
        top: 70px;
        width: auto;
        z-index: 15;

        @include tablet {
            top: 140px;
        }


        @include desktop {
            background-color: #f5f5f5;
            padding: 18px;
            top: 140px;
        }


        &:before,
        &:after {
            @extend %icomoon;

            color: $white;
            font-size: 12px;
            height: 12px;
            width: 12px;

            @include desktop {
                color: #040000;
            }
        }

        &:hover,
        &:focus {
            color: $orange1;
        }
    }

    .slick-prev {
        left: 0;

        @include desktop {
            left: 152px;
        }


        &:before {
            content: "\e910";

            @include desktop {
                margin-right: 8px;
            }
        }
    }

    .slick-next {
        right: 0;

        @include desktop {
            right: 152px;
        }


        &:before {
            display: none;
        }

        &:after {
            content: "\e90a";

            @include desktop {
                margin-left: 8px;
            }
        }
    }

    .slick-page {
        color: #222;
        display: none;
        font-size: 12px;

        @include desktop {
            display: inline-block;
        }
    }

    .slick-dots {
        li {
            height: 14px;
            margin: 0 5px;
            width: 14px;
        }

        button {
            @include transition(background-color, $animTime, $easeOutExpo);

            background-color: transparent;
            border: 2px solid $orange1;
            border-radius: 50%;
            height: 14px;
            padding: 0;
            width: 14px;

            &:before {
                font-size: 0;
            }
        }

        .slick-active {
            button {
                background-color: $orange1;
            }
        }
    }

    .node-publication {
        position: relative;
    }

    .recently-desc {
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
        bottom: 0;
        color: #FFFFFF;
        padding: 20px;
        position: absolute;
        width: 100%;

        .field-name-field-duration {
            @include font(13);

            background-color: #E8E8E8;
            border-radius: 20px;
            color: #222222;
            display: inline-block;
            margin-right: 20px;
            margin-bottom: 10px;
            padding: 10px 15px;
            position: relative;
        }

        .contributed-by {
            font-weight: $bold;
        }

        .content-title {
            @include font(24);
            @include transition(color, $animTime, $easeOutExpo);

            color: #FFFFFF;
            display: block;
            font-family: "Crimson Text";
            font-weight: bold;
            line-height: 1.25;

            &:hover,
            &:focus {
                // color: #fff;
                // text-decoration: none;
                color: #D73704;
            }
        }

        .content-link{
            color: $white;
            margin-top: 10px;
            @include font(13);


            &:hover,
            &:focus {
                // color: #fff;
                // text-decoration: none;
                color: #D73704;
            }
        }
    }

    .slick-current {
        .recently-desc {
            opacity: 1;
            visibility: visible;
        }
    }

    .field-name-field-duration {
        .field-items {
            &:before {
                @extend %icomoon;

                color: $orange1;
                display: inline-block;
                font-size: 20px;
                height: 20px;
                vertical-align: top;
                width: 20px;
            }
        }

        .field-item {
            border-left: 1px solid #bfbfbf;
            margin-left: 10px;
            padding: 1px 0 1px 10px;
            display: inline-block;
            vertical-align: top;
        }
    }

    .video {
        .field-name-field-duration {
            .field-items {
                &:before {
                    content: "\e90f";
                }
            }
        }
    }

    .podcast {
        .field-name-field-duration {
            .field-items {
                &:before {
                    content: "\e90d";
                }
            }
        }
    }

    .article {
        .field-name-field-duration {
            .field-items {
                &:before {
                    content: "\e90c";
                }
            }
        }
    }

    .slick-slide {
        a {
            display: block;
        }
    }
}

.front {
    #block-views-recently-forged-ideas-block {
        .views-row {
            position: relative;
        }

        .course {
            min-height: 300px;
            overflow: hidden;

            img {
                @extend .sr-only;
            }

            &__item,
            &__wrap {
                min-height: 350px;
            }

            &__wrap {
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                position: relative;
            }

            &__time {
                @include font(13);

                background-color: #E8E8E8;
                border-radius: 20px;
                color: #222222;
                display: inline-block;
                margin-right: 20px;
                margin-bottom: 10px;
                padding: 10px 15px;
                position: relative;

                &:before {
                    @extend %icomoon;

                    content: $icon-clock;
                    color: $orange1;
                    font-size: 17px;
                    height: 17px;
                    margin-right: 20px;
                    width: 17px;
                }

                &:after {
                    background-color: #BABABA;
                    bottom: 0;
                    content: '';
                    display: block;
                    height: 20px;
                    margin: auto;
                    position: absolute;
                    left: 40px;
                    top: 0;
                    width: 2px;
                }
            }

            &__detail {
                background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
                bottom: 0;
                color: #FFFFFF;
                padding: 20px;
                position: absolute;
                width: 100%;
            }

            &__contribution {
                @include font(13);

                display: block;
                margin: 10px 0;
            }

            &__title {
                @include font(24);
                @include transition(color, $animTime, $easeOutExpo);

                color: #FFFFFF;
                display: block;
                font-family: "Crimson Text";
                font-weight: bold;
                line-height: 1.25;

                &:hover,
                &:focus {
                    // color: #fff;
                    // text-decoration: none;
                    color: #D73704;
                }
            }
        }
    }
}

#block-views-craousel-homepage-block {
    .view-craousel-homepage {
        max-width: none;
        padding: 0;
    }

    .slick-slide {
        height: 650px;
        z-index: 5;

        & > div {
            height: 100%;

            & > .views-row {
                height: 100%;
                position: relative;

                &:after {
                    background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 100%, rgba(0, 0, 0, 0) 100%);
                    bottom: 0;
                    content: '';
                    height: 360px;
                    left: 0;
                    position: absolute;
                    width: 100%;
                }
            }

            .views-field-field-slide-image {
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
                height: 100%;

                img {
                    @extend .sr-only;

                    // background-image: ();

                }
            }

            .views-field-title {
                @include font(38);

                bottom: 80px;
                font-family: "Crimson Text";
                font-weight: $bold;
                line-height: 1.25;
                left: 0;
                max-width: 620px;
                margin: auto;
                padding: 0 20px;
                position: absolute;
                right: 0;
                width: 100%;
                z-index: 10;

                a {
                    color: #FFFFFF;

                    &:hover,
                    &:focus {
                        color: #D73704;
                        -webkit-transition-property: color;
                        transition-property: color;
                        -webkit-transition-duration: 0.5s;
                        transition-duration: 0.5s;
                        -webkit-transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
                        transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
                    }
                }
            }
        }
    }

    .slick-dots {
        bottom: 35px;
        left: 0;
        width: 100%;
        z-index: 10;

        @include tablet {
            bottom: 50px;
        }


        li {
            height: auto;
            margin: 0 10px;
            width: auto;

            button {
                border: 1px solid $white;
                border-radius: 50%;
                height: 10px;
                margin-right: 0;
                width: 10px;

                &:before {
                    display: none;
                }
            }

            &.slick-active {
                button {
                    background: $white;
                }
            }
        }
    }

    .slick-prev, .slick-next {
        z-index: 10;
    }

    .slick-arrow {
        background-color: #d73704;
        bottom: 0;
        height: auto;
        padding: 14px;
        top: auto !important;
        width: auto;
        z-index: 15;

        @include tablet {
            top: 140px;
        }


        @include desktop {
            background-color: #f5f5f5;
            padding: 18px;
            top: 140px;
        }


        &:before,
        &:after {
            @extend %icomoon;

            color: $white;
            font-size: 12px;
            height: 12px;
            width: 12px;

            @include desktop {
                color: #040000;
            }
        }

        &:hover,
        &:focus {
            color: $orange1;
        }
    }

    .slick-prev {
        border-right: 1px solid #000;
        left: auto;
        right: 68px;

        &:before {
            content: "\e910";
        }
    }

    .slick-next {
        right: 20px;

        &:before {
            display: none;
        }

        &:after {
            content: "\e90a";
        }
    }

    .slick-controls {
        bottom: 20px;
        left: 20px;
        position: absolute;

        button {
            background-color: #d73704;
            bottom: 20px;
            border: 0;
            height: auto;
            padding: 11px 14px;
            width: auto;
            z-index: 15;

            @include desktop {
                background-color: #f5f5f5;
                padding: 11px;
            }


            &:before {
                @extend %icomoon;

                color: $white;
                content: $icon-pause;
                font-size: 12px;
                height: 12px;
                width: 12px;

                @include desktop {
                    color: #040000;
                    font-size: 24px;
                    height: 24px;
                    width: 24px;
                }
            }

            &:hover,
            &:focus {
                color: $orange1;
            }

            &.-paused {
                &:before {
                    content: $icon-play !important;
                }
            }
        }
    }
}
