.webform-client-form {
    background: $white;
    padding: 30px;
    margin: 60px 0;

    .webform-component {
        padding: 20px 0;
        border-bottom: 1px solid $grey5;
        display: inline-block;
        width: 100%;

        > label {
            font-family: $font-title;

            @include font(18);

            font-weight: $medium;
            float: left;
            width: 25%;
            margin-right: 1em;

            @include breakpoint(custom, 768) {
                width: 100%;
            }
        }

        .description {
            color: $grey2;

            @include font(13);

            float: left;
            clear: both;
            width: 25%;

            @include breakpoint(custom, 768) {
                width: 100%;
            }
        }

        input {
            &[type=text],
            &[type=email],
            &[type=file] {
                @include breakpoint(custom, 768) {
                    width: 100%;
                    margin: 10px 0;
                }
            }
        }

        div#edit-submitted-select-content-type {
            float: left;

            .form-item-submitted-select-content-type {
                padding: 5px 0;
            }
        }
    }

    .captcha,
    .form-actions {
        margin-top: 20px;

        @include desktop {
            margin-left: 26.6%;
        }
    }

    .form-actions {
        margin-top: 20px;

        .webform-previous {
            background: $orange1 url(../images/svg/left-arrow-white.svg) 10% center no-repeat !important;
            background-size: 7% !important;
            margin-right: 215px;
            text-align: right !important;

            @include desktop {
                background: $orange1 url(../images/svg/left-arrow-white.svg) 18px center no-repeat !important;
                background-size: 7% !important;
            }

            &:hover,
            &:focus {
                background: $white url(../images/svg/left-arrow-orange.svg) 90% center no-repeat !important;
                background-size: 5% !important;

                @include desktop {
                    background: $white url(../images/svg/left-arrow-orange.svg) 18px center no-repeat !important;
                    background-size: 7% !important;
                }
            }
        }

        input[type=submit] {
            @include button(rounded_orange_fill);

            background: $orange1 url(../images/svg/right-arrow-white.svg) 90% center no-repeat;
            background-size: 7%;
            padding: 10px 18px;
            text-align: left;

            @include desktop {
                background: $orange1 url(../images/svg/right-arrow-white.svg) 130px center no-repeat;
                background-size: 7%;
            }

            &:hover,
            &:focus {
                background: $white url(../images/svg/right-arrow-orange.svg) 95% center no-repeat;
                background-size: 7%;

                @include desktop {
                    background: $white url(../images/svg/right-arrow-orange.svg) 130px center no-repeat;
                    background-size: 7%;
                }
            }

            @include breakpoint(custom, 495) {
                display: block;
                max-width: 180px;
                margin: 0 auto 10px;
                width: 100%;
            }
        }
    }
}
