// article-detail styles
#block-views-details-view-block {

    h2 {
        @include font(18);

        font-weight: $normal;
        margin-top: 0;
    }

    p {
        @include font(14);
    }

    .block-title {
        margin-top: 65px;
    }

    .view-content {
        @include font(13);

        color: $grey1;
    }

    .views-field-field-primary-tag {
        color: #1b0f11;
        margin-bottom: 30px;

        a {
            @include font(14);

            font-weight: $medium;
        }
    }

    .content-author,
    .publication-date {
        @include font(14);

        margin-bottom: 10px;
    }

    .publication-date {
        span {
            &:before {
                @extend %icomoon;

                content: "\e90b";
                margin-right: 10px;
            }
        }
    }

    .content-author {
        span, div {
            display: inline-block !important;
            width: auto !important;
        }
    }

    .views-row {
        > div > span {
            display: block;
            position: relative;
            width: 100%;
        }
    }
}
