// Overwrite the default font path
$icomoon-font-path: $font_folder + "/icons/fonts" !default;

@import "../../typography/icons/style.scss";
@import "../../typography/icons/variables.scss";

%icomoon {
    /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}