// infographics styles
#main.-fluid {
    padding: 0;
    max-width: 100%;
}

#infographics {
    @include tablet {
        height: 100vh;
        overflow: hidden;
        margin-top: 50px;
    }


    button[type="button"] {
        background-color: transparent;
        border: 0;
        padding: 0;
    }

    .-chevron {
        svg {
            polygon {
                @include transition(opacity, $animTime, linear);

                animation: fade 5s linear infinite;
                opacity: 0;

                @for $i from 1 through 15 {
                    &:nth-child(#{$i}) {
                        animation-delay: 0.25s * ($i - 1);
                    }
                }
            }
        }
    }

    .btn {
        opacity: 0;
        position: absolute;
        transform: scale(0.5);
        visibility: hidden;
        z-index: 1;

        span {
            @include transition(color, $animTime, $easeOutExpo);

            @include centering-the-unknown;

            font-weight: bold;
        }

        &:hover,
        &:focus {
            span {
                color: #fff;
            }

            .-textBg {
                fill: #d73704;
            }
        }

        .-textBg {
            @include transition(fill, $animTime, $easeOutExpo);
        }
    }

    .btn__close {
        background-color: transparent;
        border: 0;
        height: 40px;
        padding: 0;
        width: 60px;

        &:hover,
        &:focus {
            span {
                color: #000;
            }

            svg {
                transform: translateX(-10px);
            }
        }

        svg {
            @include transition(transform, $animTime, $easeOutExpo);

            display: inline-block;
            height: auto;
            margin-top: 6px;
            margin-right: 5px;
            width: 18px;
            vertical-align: top;
        }

        span {
            @include reset-centering;

            opacity: 1;
            visibility: visible;

            @include font(14);

            display: inline-block;
            font-weight: normal;
            vertical-align: top;
            width: auto;
        }
    }

    .block__title {
        @extend .sr-only;
    }

    .block__content {
        @extend .container;
    }

    .intellectual-edge {
        height: 245px;
        left: calc(50% - (233px / 2));
        top: calc(50% - (245px / 2));
        width: 233px;
        z-index: 5;

        span {
            color: #fff;
            font-size: 13px;
            z-index: 3;
        }

        .-tech01 {
            animation: rotate-ccw 120s linear infinite;
            transform-origin: center;
        }

        .-tech02 {
            animation: rotate-ccw 30s linear infinite;
            transform-origin: center;
        }

        .-tech03 {
            animation: rotate-cw 90s linear infinite;
            transform-origin: center;
        }

        .-main {
            position: relative;
            z-index: 2;
        }

        .-extra {
            height: 153px;
            opacity: 0;
            position: absolute;
            top: 182px;
            right: -30px;
            transform: scale(0.5);
            transform-origin: 60px 35px;
            visibility: hidden;
            width: 153px;
            z-index: 1;

            ul {
                font-size: 11px;
                font-weight: bold;
                line-height: 1.25;
                list-style-type: disc;
                left: 56px;
                position: absolute;
                top: 50px;
                text-align: left;
            }

            .-circle {
                animation: rotate-cw 60s linear infinite;
                transform-origin: center;
            }
        }
    }

    .jpmet {
        height: 314px;
        left: calc(50% - (314px / 2)); // 320px
        top: calc(50% - (314px / 2)); // 300px
        width: 314px;
        z-index: 4;

        span {
            font-size: 42px;
            line-height: 1;
        }

        .-tech01 {
            animation: rotate-cw 45s linear infinite;
            transform-origin: 118px 118px;
        }

        .-tech02 {
            animation: rotate-ccw 120s linear infinite;
            transform-origin: 118.5px 118.5px;
        }
    }

    .areas-of-study {
        height: 335px;
        left: calc(50% - (335px / 2)); // 610px
        top: calc(50% - (335px / 2)); // 230px
        width: 335px;

        circle {
            @include transition(fill, $animTime, $easeOutExpo);
        }

        &:hover,
        &:focus {
            circle {
                fill: #d73704;
            }
        }

        span {
            color: $white;
            font-size: 18px;
        }

        .-tech01 {
            animation: rotate-cw 60s linear infinite;
            transform-origin: 138px 142px;
        }

        .-tech02 {
            animation: rotate-ccw 30s linear infinite;
            transform-origin: 138px 142px;
        }

        .-tech03 {
            animation: rotate-ccw 80s linear infinite;
            transform-origin: 138px 142px;
        }

        .-tech04 {
            animation: rotate-ccw 25s linear infinite;
            transform-origin: 138px 142px;
        }

        .-tech05 {
            animation: rotate-cw 75s linear infinite;
            transform-origin: 138px 142px;
        }

        .-tech06 {
            animation: rotate-cw 20s linear infinite;
            transform-origin: 138px 142px;
        }

        .-tech07 {
            animation: rotate-cw 90s linear infinite;
            transform-origin: 138px 142px;
        }
    }

    .joint-behaviour {
        height: 421px;
        left: calc(50% - (421px / 2)); // 366px
        top: calc(50% - (421px / 2)); // -55px
        width: 421px;

        span {
            color: $white;
            font-size: 24px;
            right: -90px;
        }

        .-tech01 {
            animation: rotate-ccw 60s linear infinite;
            transform-origin: 139px 142px;
        }

        .-tech02 {
            animation: rotate-cw 15s linear infinite;
            transform-origin: 139px 142px;
        }

        .-tech03 {
            animation: rotate-ccw 90s linear infinite;
            transform-origin: 139px 142px;
        }

        .-tech04 {
            animation: rotate-cw 100s linear infinite;
            transform-origin: 139px 142px;
        }

        .-tech05 {
            animation: rotate-cw 60s linear infinite;
            transform-origin: 140px 142px;
        }

        .-tech06 {
            animation: rotate-ccw 200s linear infinite;
            transform-origin: 140px 142px;
        }

        .-tech07 {
            animation: rotate-ccw 100s linear infinite;
            transform-origin: 140px 142px;
        }
    }

    .bg {
        height: inherit;
        position: relative;
        z-index: -1;

        &__elem {
            opacity: 0;
            position: absolute;
            transform: scale(0.5);
            visibility: hidden;
        }

        .-satellite {
            height: 93px;
            left: calc(50% - (115px / 2)); // 230px;
            top: calc(50% - (93px / 2)); // 240px;
            width: 115px;

            svg {
                animation: rotate-ccw 120s linear infinite;
                transform-origin: 99px 47px;
            }
        }

        .-elem01 {
            height: 16px;
            left: calc(50% - (16px / 2)); // 260px;
            top: calc(50% - (16px / 2)); // 160px;
            width: 16px;
        }

        .-elem02 {
            height: 8px;
            left: calc(50% - (8px / 2)); // 300px;
            top: calc(50% - (8px / 2)); // 100px;
            width: 8px;
        }

        .-elem03 {
            height: 70px;
            left: calc(50% - (70px / 2)); // -180px;
            top: calc(50% - (70px / 2)); // -400px;
            width: 70px;
        }

        .-elem04 {
            height: 38px;
            left: calc(50% - (38px / 2)); // 340px;
            top: calc(50% - (38px / 2)); // 240px;
            width: 38px;
        }

        .-elem05 {
            height: 38px;
            left: calc(50% - (38px / 2)); // 230px;
            top: calc(50% - (38px / 2)); // 240px;
            width: 38px;
        }

        // Satellite UL
        .-elem06 {
            transform: rotate(-33.33deg);
        }

        // Satellite UR
        .-elem07 {
            transform: rotate(162.43deg);
        }

        // Satellite BR
        .-elem08 {
            transform: rotate(-145.02deg);
        }

        .-elem09 {
            height: 34px;
            left: calc(50% - (150px / 2));
            top: calc(50% - (34px / 2));
            transform: scale(1);
            width: 150px;
        }

        .-elem10 {
            height: 34px;
            left: calc(50% - (150px / 2));
            top: calc(50% - (34px / 2));
            transform: scale(-1, 1);
            width: 150px;
        }

        .-elem11 {
            height: 28px;
            left: calc(50% - (118px / 2));
            top: calc(50% - (28px / 2));
            transform: scale(-1, 1);
            width: 118px;
        }
    }

    .splash {
        height: inherit;
        position: relative;
    }

    .slide {
        margin-bottom: 20px;

        @include tablet {
            margin-bottom: 0;
        }

        &__list {
            list-style: none;
        }


        &__content {
            @include tablet {
                height: 620px;
            }
        }

        &__detail {
            display: none;
            margin-top: 20px;

            @include desktop {
                display: none !important;
            }


            ul {
                list-style: disc;
                padding: 0 0 0 40px;
            }
        }

        &__ie {
            h2 {
                position: relative;
            }

            span {
                @include centering-the-unknown;

                font-size: 11px;
                font-weight: bold;
                text-align: center;
                z-index: 3;

                @include tablet {
                    @include reset-centering;
                }
            }

            .btn__close {
                position: absolute;
                right: 0;
                top: 0;
                z-index: 5;

                svg {
                    margin-top: 4px;
                }
            }

            .-main {
                display: block;
                margin: 0 auto 110px;
                width: 240px;
            }

            .-extra {
                height: 153px;
                left: 140px;
                margin: auto;
                position: absolute;
                top: 190px;
                right: 20px;
                transform-origin: 60px 35px;
                width: 153px;
                z-index: 1;

                ul {
                    font-size: 11px;
                    font-weight: bold;
                    line-height: 1.25;
                    list-style-type: disc;
                    left: 56px;
                    position: absolute;
                    top: 50px;
                    text-align: left;
                }

                .-circle {
                    animation: rotate-cw 60s linear infinite;
                    transform-origin: center;
                }
            }
        }

        &__jpmet,
        &__paos,
        &__jb {
            @include tablet {
                opacity: 0;
                padding: 0 20px;
                position: absolute;
                left: 35%;
                top: 30px;
                transform: translateX(75px);
                visibility: hidden;
                width: 65%;
            }


            @include desktop {
                left: 60%;
                top: 60px;
                width: 40%;
            }


            h2 {
                margin: auto;
                margin-bottom: 20px;
                position: relative;
                text-align: center;

                span {
                    @include centering-the-unknown;

                    font-weight: bold;
                }

                @include tablet {
                    position: absolute;
                    width: 1px;
                    height: 1px;
                    padding: 0;
                    margin: -1px;
                    overflow: hidden;
                    clip: rect(0, 0, 0, 0);
                    border: 0;
                }
            }

            h3 {
                @include font(24);

                margin: 0 0 20px;
            }

            .slide__list {
                & > li {
                    border-bottom: 2px solid #cddae8;
                    margin-bottom: 20px;
                    padding-bottom: 20px;
                    position: relative;

                    @include tablet {
                        opacity: 0;
                        transform: translateY(25px);
                        visibility: hidden;
                    }


                    p {
                        margin-bottom: 0;
                    }
                }
            }

            .btn__toggle {
                padding-right: 30px;
                position: relative;
                text-align: left;
                width: 100%;

                @include desktop {
                    padding-right: 0;
                }


                &:before,
                &:after {
                    @include transition(transform, $animTime, $easeInOutExpo);

                    background-color: #000;
                    bottom: 0;
                    content: '';
                    height: 2px;
                    display: block;
                    margin: auto;
                    position: absolute;
                    right: 0;
                    top: 0;
                    width: 10px;

                    @include desktop {
                        display: none;
                    }
                }

                &:before {
                    transform: rotate(-45deg);
                }

                &:after {
                    transform: rotate(45deg);
                    right: 6px;
                }

                &.-active {
                    &:before {
                        transform: rotate(45deg);
                    }

                    &:after {
                        transform: rotate(-45deg);
                    }
                }
            }

            p {
                @include font(14);

                margin-bottom: 20px;
            }

            .btn__close {
                margin-bottom: 20px;
                position: static;
            }
        }

        &__jpmet {
            svg {
                max-width: 200px;
            }

            .-tech01 {
                animation: rotate-cw 45s linear infinite;
                transform-origin: 118px 118px;
            }

            .-tech02 {
                animation: rotate-ccw 120s linear infinite;
                transform-origin: 118.5px 118.5px;
            }
        }

        &__paos {
            h2 {
                span {
                    @include font(10);

                    color: $white;
                }
            }

            svg {
                max-width: 250px;
            }

            li {
                padding-left: 60px;

                svg {
                    left: 0;
                    position: absolute;
                    top: 0;
                    width: 40px;
                }
            }

            .-tech01 {
                animation: rotate-cw 60s linear infinite;
                transform-origin: 138px 142px;
            }

            .-tech02 {
                animation: rotate-ccw 30s linear infinite;
                transform-origin: 138px 142px;
            }

            .-tech03 {
                animation: rotate-ccw 80s linear infinite;
                transform-origin: 138px 142px;
            }

            .-tech04 {
                animation: rotate-ccw 25s linear infinite;
                transform-origin: 138px 142px;
            }

            .-tech05 {
                animation: rotate-cw 75s linear infinite;
                transform-origin: 138px 142px;
            }

            .-tech06 {
                animation: rotate-cw 20s linear infinite;
                transform-origin: 138px 142px;
            }

            .-tech07 {
                animation: rotate-cw 90s linear infinite;
                transform-origin: 138px 142px;
            }
        }

        &__jb {
            h2 {
                span {
                    color: $white;
                    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
                }
            }

            svg {
                max-width: 250px;
            }

            .-tech01 {
                animation: rotate-ccw 60s linear infinite;
                transform-origin: 139px 142px;
            }

            .-tech02 {
                animation: rotate-cw 15s linear infinite;
                transform-origin: 139px 142px;
            }

            .-tech03 {
                animation: rotate-ccw 90s linear infinite;
                transform-origin: 139px 142px;
            }

            .-tech04 {
                animation: rotate-cw 100s linear infinite;
                transform-origin: 139px 142px;
            }

            .-tech05 {
                animation: rotate-cw 60s linear infinite;
                transform-origin: 140px 142px;
            }

            .-tech06 {
                animation: rotate-ccw 200s linear infinite;
                transform-origin: 140px 142px;
            }

            .-tech07 {
                animation: rotate-ccw 100s linear infinite;
                transform-origin: 140px 142px;
            }

            .slide__detail {
                display: block !important;
            }
        }

        .btn__close {
            display: none;

            @include tablet {
                display: block;
            }
        }
    }

    .circles {
        position: relative;

        @include tablet {
            opacity: 0;
            position: absolute;
            transform: scale(0.5);
            visibility: hidden;
        }


        span {
            @include centering-the-unknown;
            @include font(15);

            font-weight: bold;
            line-height: 1.2;
            text-align: center;
        }

        svg {
            path {
                transform-origin: center;
            }

            .-tech01 {
                animation: rotate-cw 30s linear infinite;
            }

            .-tech02 {
                animation: rotate-ccw 15s linear infinite;
            }

            .-tech03 {
                animation: rotate-cw 45s linear infinite;
            }

            .-tech04 {
                animation: rotate-ccw 5s linear infinite;
            }

            .-tech05 {
                animation: rotate-cw 60s linear infinite;
            }
        }

        &.-oaei {
            margin: 0 auto 20px;
            width: 200px;

            @include tablet {
                height: 255px;
                left: calc(50% - (255px / 2));
                top: calc(50% - (255px / 2));
                width: 255px;
            }


            span {
                @include tablet {
                    font-size: 18px;
                }
            }
        }

        &.-itea {
            margin: 0 auto 20px;
            width: 200px;

            @include tablet {
                height: 292px;
                left: calc(50% - (292px / 2));
                top: calc(50% - (292px / 2));
                width: 292px;
            }


            span {
                @include tablet {
                    font-size: 22px;
                }
            }
        }

        &.-cd {
            margin: 0 auto 20px;
            width: 200px;

            @include tablet {
                height: 195px;
                left: calc(50% - (195px / 2));
                top: calc(50% - (195px / 2));
                width: 195px;
            }


            span {
                @include tablet {
                    font-size: 13px;
                }
            }
        }

        &.-daec {
            margin: 0 auto 20px;
            width: 200px;

            @include tablet {
                height: 255px;
                left: calc(50% - (255px / 2));
                top: calc(50% - (255px / 2));
                width: 255px;
            }


            span {
                @include tablet {
                    font-size: 18px;
                }
            }
        }

        &.-sdm {
            margin: 0 auto 20px;
            width: 200px;

            @include tablet {
                height: 195px;
                left: calc(50% - (195px / 2));
                top: calc(50% - (195px / 2));
                width: 195px;
            }


            span {
                @include tablet {
                    font-size: 18px;
                }
            }
        }

        &.-sces {
            margin: 0 auto 20px;
            width: 200px;

            @include tablet {
                height: 255px;
                left: calc(50% - (255px / 2));
                top: calc(50% - (255px / 2));
                width: 255px;
            }


            span {
                @include tablet {
                    font-size: 18px;
                }
            }
        }

        &.-hcu {
            margin: 0 auto 20px;
            width: 200px;

            @include tablet {
                height: 255px;
                left: calc(50% - (255px / 2));
                top: calc(50% - (255px / 2));
                width: 255px;
            }


            span {
                @include tablet {
                    font-size: 21px;
                }
            }
        }
    }

    .callout {
        display: none;

        @include tablet {
            display: block;
            opacity: 0;
            position: absolute;
            left: 40%;
            top: 60px;
            transform: translateX(60%);
            visibility: hidden;
            width: 60%;
        }


        svg {
            width: 100%;
        }

        &__title {
            color: #fff;
            height: 56px;
            position: absolute;
            width: 118px;

            @include desktop {
                left: 71px;
                top: 146px;
            }


            @include large-desktop {
                left: 88px;
                top: 170px;
            }


            span {
                @include centering-the-unknown;

                font-weight: bold;
                text-align: center;
                width: 100%;
            }
        }

        &__detail {
            height: 175px;
            position: absolute;
            top: 70px;
            right: 85px;

            @include desktop {
                left: 236px;
            }


            @include large-desktop {
                left: 270px;
            }


            h2 {
                @include font(28);

                color: #a7dce2;
                margin-top: 0;
            }

            p {
                color: #fff;
                line-height: 1.5;
                margin: 15px 0;

                &:only-child {
                    margin: 0;
                }

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        &__elem {
            opacity: 0;
            position: absolute;
            transform: translateX(75px);
            visibility: hidden;
        }

        .-elem01 {
            left: 186px;
            top: -75px;
            width: 46px;
        }

        .-elem02 {
            left: 180px;
            top: 410px;
            width: 28px;
        }

        .-elem03 {
            left: 510px;
            top: 420px;
            width: 54px;
        }

        .-elem04 {
            left: 70px;
            top: -43px;
            width: 110px;
        }

        .-elem05 {
            left: -83px;
            top: 90px;
            width: 166px;
        }

        .-elem06 {
            left: 580px;
            top: 380px;
            width: 166px;
        }

        .-elem07 {
            left: 430px;
            top: -70px;
            width: 61px;

            svg {
                animation: rotate-ccw 120s linear infinite;
                transform-origin: 53px 25px;
            }
        }

        .-elem08 {
            left: 20px;
            top: 385px;
            width: 97px;

            svg {
                animation: rotate-ccw 120s linear infinite;
                transform-origin: 84px 39px;
            }
        }

        .btn__close {
            @include reset-centering;

            opacity: 1;
            position: absolute;
            right: 0;
            visibility: visible;
            width: 40px;

            &:before,
            &:after {
                @include absolute-center;

                background-color: #000;
                bottom: 12px;
                content: '';
                display: block;
                height: 2px;
                left: 12px;
                width: 14px;
            }

            &:before {
                transform: rotate(45deg);
            }

            &:after {
                transform: rotate(-45deg);
            }
        }
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }
}

@keyframes rotate-cw {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes rotate-ccw {
    100% {
        transform: rotate(-360deg);
    }
}
