#block-views-51eaf2d702f5eec8239032397e1beb68 {
    &.block-views.contextual-links-region {
        background: url(../images/computerbits.png) repeat-x center;
        background-size: contain;

        .block-title {
            @include font(32);

            margin: 50px auto;
            text-align: center;

            @include large-desktop {
                max-width: 1160px;
            }
        }

        .block__content {
            margin: 0 auto;

            @include large-desktop {
                max-width: 1160px;
            }


            .core-study-area {
                width: 25%;
                float: left;
                padding: 0 30px 30px;
                box-sizing: border-box;
                text-align: center;

                @include breakpoint(custom, 768) {
                    width: 50%;
                    padding-bottom: 30px;
                }


                @include breakpoint(custom, 420) {
                    width: 100%;
                }


                a {
                    @include transition(color, $animTime, $easeOutExpo);
                    @include font(18);

                    font-weight: $medium;
                    padding: 110px 0 15px 0;
                    float: left;
                    width: 100%;

                    &:hover {
                        color: $orange1;

                        & + .field-content {
                            visibility: visible;
                        }
                    }
                }

                &.views-row-1 {
                    a {
                        background: url(../images/svg/Security.svg) no-repeat center top;
                    }
                }

                &.views-row-2 {
                    a {
                        background: url(../images/svg/Command.svg) no-repeat center top;
                    }
                }

                &.views-row-3 {
                    a {
                        background: url(../images/svg/Warfare.svg) no-repeat center top;
                    }
                }

                &.views-row-4 {
                    a {
                        background: url(../images/svg/Technology.svg) no-repeat center top;
                    }
                }

                .field-content {
                    visibility: hidden;
                }
            }
        }

        @include clearfix;
    }
}
