// footer styles
#footer {
    background-color: $background2;
    padding-top: 20px;

    @include tablet {
        padding-top: 50px;
    }


    a {
        @include transition(color, $animTime, $easeOutExpo);

        color: $grey4;
        font-weight: $normal !important;

        &:hover,
        &:focus {
            color: $orange1;
        }
    }

    .-js-back-to-top {
        @include transition((color, background-color, opacity), $animTime, $easeOutExpo);
        @include font(10);

        background-color: #e1e1e1;
        border: 0;
        color: #1d1113;
        opacity: 0;
        padding: 8px 15px;
        padding-right: 35px;
        position: fixed;
        bottom: 0;
        right: 24px;
        text-transform: uppercase;

        &.-fixed {
            bottom: auto;
            position: absolute;
            top: -45px;
        }

        &:before,
        &:after {
            @extend %icomoon;

            @include transition((opacity, top), $animTime, $easeOutExpo);

            content: "\e912";
            position: absolute;
            right: 15px;
        }

        &:before {
            opacity: 1;
            top: 10px;
        }

        &:after {
            opacity: 0;
            top: 20px;
        }

        &:hover,
        &:focus {
            background-color: $orange1;
            color: $white;

            &:before {
                opacity: 0;
                top: 0;
            }

            &:after {
                opacity: 1;
                top: 10px;
            }
        }
    }

    .footer__logo {
        width: 100%;
    }

    .container {
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 30px;
        margin-top: 30px;
        justify-content: space-between;
    }

    .region-footer {
        @extend .container;
    }

    .siteLogo__img {
        margin-top: 6px;
        max-width: 110px;

        @include tablet {
            max-width: 218px;
        }


        svg {
            width: 100%;
        }
    }

    .menu {
        @include normalise-list;
        @include clearfix;

        li {
            display: inline-block;
        }
    }

    #block-menu-block-1 {
        border-bottom: 1px solid #c0c0c0;
        padding-bottom: 15px;

        @include tablet {
            margin-top: 30px;
        }


        .menu {
            text-align: center;
            position: relative;
            display: inline-block;
            width: 100%;
        }

        .menu__item {
            float: left;
            margin-bottom: 15px;

            // width: 50%;

            width: auto;

            @include mobile {
                width: auto;
                float: none;
                padding-right: 7%;

                &:nth-child(4) {
                    padding-right: 0px;
                }
            }


            a {
                @include font(14);

                font-weight: $bold;
                padding-right: 20px;

                @include mobile {
                    // width: auto;
                    // padding-left: 20px;
                    padding-right: 0px;
                }
            }
        }
    }

    #block-bean-footer-copyright {
        padding: 8px 0 15px;
        width: 100%;

        @include tablet {
            padding: 15px 0;
        }


        @include desktop {
            width: 50%;
        }


        p {
            @include font(14);

            margin: 0;
        }
    }

    #block-menu-block-2 {
        @include font(14);

        font-weight: $bold;
        padding: 15px 0 8px;
        text-align: left;
        width: 100%;

        @include tablet {
            padding: 15px 0;
        }


        @include desktop {
            text-align: right;
            width: 50%;
        }


        .menu {
            li {
                &:after {
                    content: '|';
                    margin: 0 10px 0 13px;
                }

                &:last-child {
                    &:after {
                        display: none;
                    }
                }
            }

            a {
                margin-bottom: 15px;
            }
        }
    }
}
