// link styles

// unvisited link
a:link {
    color: $grey1;
}

// visited link
a {
    word-break: break-word;

    &:hover,
    &:active,
    &:focus {
        color: $orange1;
    }

    &:visited {
        color: $grey1;

        &:hover{
          color: $orange1;
        }
    }
}

//the below to be deleted, just testing elements
#panels-ipe-regionid-main {
    .panels-ipe-portlet-marker {
        background: $grey2;
        padding: 50px;

        a {
            @include button(rounded_white);
        }
    }
}

.view-latest-news {
    a {
        @include button(rounded_grey);
    }
}

.view-home-page-blog-articles {
    a {
        @include button(square_grey);
    }
}

.page__title {
    font-family: $font-title;
}

.messages {
    font-family: $font-main;
}
