// Example: @include button(no_chevron_red);
@mixin button($style: none) {
    @include transition(all, $animTime, $easeOutExpo);

    background: transparent;
    border-radius: 25px;
    border: 1px solid $grey1;
    color: $grey1;

    @include font(13);

    font-weight: 600;
    font-family: $font-main;
    padding: 10px 15px;
    padding-right: 40px;
    position: relative;
    text-decoration: none;
    transition: all 0.3s;

    &:before,
    &:after {
        @include transition(all, $animTime, $easeOutExpo);

        color: $grey1;
        content: "\e90a";;
        font-family: 'icomoon';
        position: absolute;
        top: 12px;
        right: 15px;
    }

    &:before {
        opacity: 0;
        right: 30px;
    }

    &:hover,
    &:focus {
        color: $orange1;
        border-color: $orange1;

        &:after {
            color: $orange1;
        }
    }

    @if $style == rounded_white {
        color: $white;
        border-color: $white;

        &:before,
        &:after {
            color: $white;
        }

        &:hover,
        &:focus {
            color: $grey1;
            border-color: $grey1;

            &:before {
                color: $grey1;
                right: 15px;
                opacity: 1;
            }

            &:after {
                color: $grey1;
                right: 0;
                opacity: 0;
            }
        }
    }

    @if $style == rounded_orange {
        color: $orange1;
        border-color: $orange1;

        &:before,
        &:after {
            color: $orange1;
        }

        &:hover,
        &:focus {
            background: $orange1;
            color: $white;

            &:before {
                color: $white;
                right: 15px;
                opacity: 1;
            }

            &:after {
                color: $white;
                right: 0;
                opacity: 0;
            }
        }
    }

    @if $style == rounded_orange_fill {
        color: $white;
        background: $orange1;
        border-color: $orange1;

        &:after {
            color: $orange1;
        }

        &:hover,
        &:focus {
            color: $orange1;
            background-color: $white;

            &:after {
                color: $orange1;
            }
        }
    }

    @if $style == square_grey {
        border-radius: 0;
        text-transform: uppercase;
        padding: 8px;
        padding-right: 40px;

        &:before,
        &:after {
            content: $icon-upper-left-arrow;
        }

        &:before {
            opacity: 0;
            top: 25px;
            right: 30px;
        }

        &:after {
            top: 10px;
        }

        &:hover,
        &:focus {
            &:before {
                color: $orange1;
                opacity: 1;
                top: 10px;
                right: 15px;
            }

            &:after {
                opacity: 0;
                top: 5px;
                right: 5px;
            }
        }
    }
}
