// core-study-toc styles
.section-core-areas-of-study {
    #edit-sort-bef-combine-wrapper {
        display: none;
    }

    .core-areas-of-study-tag-description {
        @include font(17);

        line-height: 1.75;
        margin: 0 auto 60px;
        max-width: 800px;
    }

    .btn-toggle {
        background-color: transparent;
        border: 0;
        height: 20px;
        width: 20px;
        padding: 0;
        position: absolute;
        right: 0;
        top: 0;

        &:after {
            @extend %icomoon;

            @include transition((color, transform), $animTime, $easeOutExpo);

            backface-visibility: visible;
            content: $icon-chevron-down;
            font-size: 1em;
            color: $orange2;
            height: 1em;
            margin-bottom: 15px;
            position: absolute;
            right: 2px;
            transform: none;
            top: 2px;
            width: 1em;
        }

        &.-collapse {
            &:after {
                color: #14080a;
                transform: rotateX(180deg);
            }
        }
    }

    .form-type-checkbox {
        input[type="checkbox"] {
            padding-right: 20px;
        }
    }

    .page__title {
        @include font(36);

        margin: 50px 0;
    }

    .sidebar_first {
        display: none;
        margin-bottom: 20px;

        @include tablet {
            display: block !important;
            float: left;
            width: 33.333333%;
        }


        &__wrap {
            display: none;

            @include tablet {
                display: block;
            }
        }

        &__filter {
            background-color: $white;
            box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.16);
            margin-bottom: 20px;
            padding: 25px 15px;
            padding-right: 70px;
            position: relative;

            @include tablet {
                display: none;
            }


            p {
                @include font(15);

                font-family: 'Crimson Text', serif;
                font-weight: $medium;
            }
        }

        &__toggle {
            @include transition(background-color, $animTime, $easeOutExpo);

            background-color: #d73704;
            border: 0;
            bottom: 0;
            height: 40px;
            margin: auto;
            position: absolute;
            top: 0;
            right: 15px;
            width: 40px;

            &:before {
                @extend %icomoon;

                @include transition(transform, $animTime, $easeOutExpo);

                backface-visibility: visible;
                content: $icon-chevron-down;
                color: $white;
                display: block;
                font-size: 12px;
                position: relative;
                transform: none;
            }

            &.-active {
                background-color: #14080a;

                &:before {
                    transform: rotateX(180deg);
                }
            }
        }

        .views-submit-button {
            display: none;
        }
    }

    .view-core-areas-of-study {
        @include tablet {
            float: left;
            padding-left: 20px;
            width: 66.666667%;
        }


        .views-row,
        .view-empty {
            background: $white;
            box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.16);
            margin-bottom: 20px;
            padding: 1.2rem;

            &:first-child {
                margin-top: 0;
            }

            .node {
                position: relative;
            }

            .share {
                position: absolute;
                right: 0;

                @include tablet {
                    // right: 70px;
                }
            }

            .group-left {
                margin-bottom: 15px;

                @include tablet {
                    float: left;
                    width: 33.333333%;
                    height: 100%;
                }


                img {
                    height: 150px;
                    max-height: 100%;
                    max-width: 100%;
                    object-fit: cover;
                    width: 100%;
                }
            }

            .group-right {
                @include tablet {
                    float: right;
                    width: 66.666667%;
                    padding-left: 1rem;
                }


                h2 {
                    margin: 0;
                    padding: 0.8rem 0;
                }
            }
        }

        .content-duration {
            background: $background2;
            border-radius: 20px;
            margin-bottom: 15px;
            padding: 10px 20px !important;
            width: 135px;

            & > div > div {
                &:before {
                    @extend %icomoon;

                    border-right: 1px solid #bfbfbf;
                    color: $orange1;
                    display: inline-block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 10px;
                    padding-right: 10px;
                    vertical-align: top;
                    width: 30px;
                }
            }

            .field-item {
                display: inline-block;
                vertical-align: top;
            }
        }

        .content-date-of-publication {
            display: inline;
            padding-right: 1rem;
            float: left;

            &:before {
                @extend %icomoon;

                content: "\e90b";
                display: inline-block;
                font-size: 14px;
                height: 14px;
                vertical-align: top;
                margin-right: 10px;
                width: 14px;
            }
        }

        .content-author {
            display: inline;
        }

        .field-name-body {
            display: block;
            padding: 0.5rem 0;
        }

        .link-to {
            a {
                @include font(13);

                &:before {
                    @extend %icomoon;

                    color: $orange1;
                    content: "\e905";
                    display: inline-block;
                    font-size: 20px;
                    height: 20px;
                    margin-right: 10px;
                    position: relative;
                    text-decoration: none;
                    top: 4px;
                    width: 20px;
                }
            }
        }
    }

    .video,
    .Video {
        .content-duration {
            .field-items {
                &:before {
                    content: "\e90f";
                }
            }
        }
    }

    .podcast,
    .Podcast {
        .content-duration {
            .field-items {
                &:before {
                    content: "\e90d";
                }
            }
        }
    }

    .article,
    .Article {
        .content-duration {
            .field-items {
                &:before {
                    content: "\e90c";
                }
            }
        }
    }

    .region-sidebar-first {
        background: $white;
        box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.16);
        padding: 0;

        input[type=checkbox] + label {
            @include font(13);

            display: block;
            font-weight: $bold;
            padding-left: 35px;
            padding-right: 30px;

            &:after {
                background: transparent;
                border-right: 4px solid $orange1 !important;
                border-bottom: 4px solid $orange1 !important;
                border: 0;
                color: transparent;
                font-size: 0;
                left: 10px;
                line-height: 0;
                margin: 0;
                transform: rotate(45deg);
                top: -5px;
                width: 10px;
            }
        }

        .views-exposed-widget {
            border-bottom: 1px solid #dddddd;
            float: none;
            padding: 15px;

            &:last-child {
                border-bottom: 0;
            }

            & > label {
                @include font(18);

                display: block;
                font-family: 'Crimson Text', serif;
                font-weight: $medium;
                margin-bottom: 10px;
            }

            .form-text {
                margin-bottom: 0 !important;
            }
        }

        .form-text {
            margin-bottom: 20px;
            width: 100%;
        }

        //hide this area as it is a duplicate because BE do what BE do
        .form-disabled {
            display: none;
        }

        .item-list {
            width: 100%;

            ul {
                @include normalise-list;
            }
        }

        .taxonomy-tree {
            li {
                margin: 15px 0;
                position: relative;

                &:first-child {
                    margin-top: 15px;
                }

                &:last-child {
                    margin-bottom: 0;
                }

                li {
                    margin: 15px 0;

                    &:first-child {
                        margin-top: 0;
                    }
                }
            }

            //header items for nested lists
            > li {
                &:after {
                    background-color: #e1e1e1;
                    content: '';
                    display: block;
                    height: 1px;
                    left: 45px;
                    position: absolute;
                    width: calc(100% - 55px);
                }

                &:last-child {
                    &:after {
                        display: none;
                    }
                }

                > .form-item-field-tags- {
                    padding-bottom: 15px;
                }

                //2nd level
                .item-list {
                    padding-bottom: 1em;

                    ul {
                        // display: none;
                        padding-left: 0.8em;

                        li {
                            label {
                                font-weight: initial;
                                display: block;
                                width: 100%;
                            }
                        }
                    }
                }
            }
        }

        .views-widget-filter-field_media_type,
        .views-widget-filter-field_knowledge_level {
            input {
                @extend .sr-only;
            }

            .form-item {
                margin-bottom: 15px;
            }
        }

        .form-checkboxes {
            .form-item {
                margin-bottom: 15px;
            }
        }
    }

    .views-submit-button {
        .form-submit {
            padding: 0 !important;
        }
    }

    .form-submit {
        color: $grey1;
        display: block;
        margin-top: 0 !important;
        margin: auto;
        padding: 10px 34px 10px 20px;
        text-align: left;
    }

    .views-widget-filter-secondary {
        .form-type-textfield {
            label,
            .description {
                display: none;
            }
        }

        .form-wrapper {
            @include font(18);

            border: 0;
            font-family: 'Crimson Text', serif;
            font-weight: $medium;
            margin: 0;
            padding: 0;
        }

        .fieldset-title {
            display: block;
            margin-bottom: 10px;
        }

        .bef-secondary-options {
            @include clearfix;

            margin: 0 -10px;

            .container-inline-date {
                clear: none;
                float: left;
                padding: 0 10px;
                width: 50%;
            }

            .form-item {
                margin-right: 0;
            }

            .form-text {
                font-size: 0.85em !important;
                font-family: 'Open Sans', sans-serif;
                font-weight: $normal;
            }
        }
    }

    .tooltip-text {
        display: none;
    }
}

#block-views-exp-core-areas-of-study-page-2 {
    @include clearfix();
    text-align: right;
    float: right;
    margin-top: -60px;

    @include desktop {
      // float: left;
      padding-left: 20px;
      width: 66.666667%;
    }
    @include breakpoint(custom, 767){
      float: none;
      margin-top: 0px;
    }


    .block__content {
        display: inline-block;
        margin-bottom: 5px;
        width: 100%;
    }

    #edit-sort-bef-combine-wrapper {
        display: block;
        padding: 0;
        width: 100%;

        label {
            @include font(18);

            font-family: 'Crimson Text', serif;
            font-weight: $medium;
            margin-right: 15px;
        }
    }

    .views-exposed-widget {
        display: none;
    }
}

#ui-datepicker-div {
    z-index: 10 !important;
}

//pagination
ul.pager{
    @include normalise-list;
  background: #fff;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.16);
  padding: 1em;

  @include clearfix();
  li{
    float: left;
    margin-right: 0.2em;
    padding: 0.5em;

    a{
      text-decoration: none;
    }
  }

  .pager-current{
    font-weight: bold;
    background: $background2;
  }

  .pager-total-pages{
    float: left;
    margin-right: 3em;
    padding: 0.5em;
  }
}
