// global-menu styles
#block-superfish-1 {
    display: none;

    @include large-desktop {
        display: block;
        float: left;
        width: 100%;
        height: 40px;
        padding: 15px 0px;
        box-sizing: content-box;
    }


    .block__content {
        @extend .container;

        padding-top: 10px;
        width: 100%;

        ul {
            @include normalise-list;

            text-align: right;

            li {
                display: inline-block;
                margin: 0 25px;

                > a {
                    @include transition(all, $animTime, $easeOutExpo);
                    @include font(14);

                    border-bottom: 5px solid transparent;
                    color: $white;
                    font-weight: $normal;
                    text-decoration: none;
                    padding: 8px 0 18px;

                    &:hover,
                    &:focus {
                        border-bottom: 5px solid $yellow;
                    }
                }

                ul {
                    background: #d73704;
                    margin: 7px 0 0;
                    z-index: 100;

                    li {
                        margin: 0;

                        a {
                            @include transition((background-color, color), $animTime, $easeOutExpo);

                            border-bottom: 1px solid #FAED95 !important;
                            color: #fff;
                            padding: 15px 20px;
                            text-align: left;

                            &:hover,
                            &:focus {
                                background: $white;
                                border-bottom: none;
                                color: $orange1;
                            }
                        }
                    }
                }
            }
        }
    }
}

//menu on mobile
.mobile-menu-button {
    background-color: transparent;
    position: absolute;
    left: 0;
    top: 10px;
    border: 0px;
    padding: 0px;
    z-index: 10;

    .line {
        background-color: $white;
        border-radius: 4px;
        display: block;
        height: 4px;
        margin: 3px auto;
        width: 20px;
    }

    @include large-desktop {
        display: none;
    }


    @media print {
        display: none;
    }
}

.mobile-menu-overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.2);
    z-index: 1000;
    transition: all 0.3s;
    opacity: 0;
    transform: translate3d(-150%, 0, 0);

    &.active {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}

.mobile-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    box-sizing: border-box;
    padding: 20px;
    background: white;
    z-index: 2000;
    overflow-x: hidden;
    overflow-y: scroll;
    transition: all 0.3s;
    transform: translate3d(-150%, 0, 0);
    width: 100%;

    @include breakpoint(custom, 720) {
        right: 40%;
    }


    @include breakpoint(custom, 640) {}


    &.active {
        transform: translate3d(0, 0, 0);
        background-color: #d73704;
    }

    .description, .primary_highlight li:first-child {
        display: none !important;
    }

    ul, li {
        list-style: none;
        margin: 0px;
        padding: 0px;
    }

    li {
        position: relative;
        display: block !important;
    }

    // Make the first link bold
    > ul {
        margin-top: 40px;

        // For the first link do not show top botder
        > li:first-child {
            > a {
                border: 0px;
            }
        }

        > .menuparent {
            > a {}
        }

        // Hide the sub menu
        ul, .level-1 {
            display: none;

            &.show {
                display: block;
            }
        }

        .level-1 {
            &.show {
                ul {
                    display: block;
                }
            }
        }
    }

    a {
        text-decoration: none;
        color: $white;
        padding: 0.8em;
        display: block;
        border-top: 1px solid $yellow;
        margin-bottom: -1px;

        &.expand {
            position: absolute;
            right: 0;

            &:after {
                font-family: 'icomoon';
                font-weight: $light;
                content: $icon-chevron-down;
                display: inline-block;
            }

            &:hover, &:active, &:focus {
                background: transparent;
                border: 0px;
            }

            &.active {
                transform: rotate(180deg);
                color: $yellow;
            }

            // This are the parents li
            + a {
                padding-right: 40px;
            }
        }

        &.active {
            font-weight: $medium;
            color: $yellow;
        }
    }

    // Indent the items on the menu
    ul {
        li {
            li {
                a {
                    padding-left: 1.6em;
                }

                li {
                    a {
                        padding-left: 2.5em;
                        font-size: 0.9em;
                    }
                }
            }

            // Sub links from the mega menu
            .level-1 {
                > ul {
                    > li {
                        a {
                            padding-left: 1.6em;
                            font-size: 1em;
                        }
                    }
                }
            }
        }
    }

    .close-mobile-menu {
        border: 0;
        height: 20px;
        float: right;
        margin-right: 10px;
        position: relative;
        padding: 8px 2px;
        width: 20px;

        &:before,
        &:after {
            background: $white;
            content: '';
            display: block;
            height: 2px;
            position: absolute;
            width: 24px;
        }

        &:before {
            transform: rotate(45deg);
        }

        &:after {
            transform: rotate(-45deg);
        }
    }
}
