// slider_home styles
.front {
  .view-craousel-homepage {
    .field-content {
      height: 800px;
    }
    .views-row{
      position: relative;
    }
    img {
      height: 100%;
      width: auto;
      margin: 0 auto;
      max-width: 100%;
      background: #000;
    }
    .slick {
      height: 100%;
    }
    .slick-arrow {
      background-color: #d73704;
      height: auto;
      padding: 14px;
      top: 50%;
      width: auto;
      z-index: 15;

      @include tablet {
        top: 140px;
      }

      @include desktop {
        background-color: #f5f5f5;
        padding: 18px;
        top: 50%;
      }

      &:before,
      &:after {
        @extend %icomoon;

        color: $white;
        font-size: 12px;
        height: 12px;
        width: 12px;

        @include desktop {
          color: #040000;
        }
      }

      &:hover,
      &:focus {
        color: $orange1;
      }
    }

    .slick-prev {
      left: 0;
      &:before {
        content: '\e910';

        @include desktop {
          margin-right: 8px;
        }
      }
    }

    .slick-next {
      right: 0;
      &:before {
        display: none;
      }

      &:after {
        content: '\e90a';

        @include desktop {
          margin-left: 8px;
        }
      }
    }
    .views-field-title{
      @include transition((opacity, visibility), $animTime, $easeOutExpo);

      position: absolute;
      bottom: 0;
      width: 100%;
      text-align: center;
      z-index: 200;
      // background: linear-gradient(to top, rgba(0,0,0,0.7) 70%, rgba(0,0,0,0.1) 20%);
      a{
        color: $white;
      }
    }
  }
}
