#block-bean-homepage-hero-block {
    position: relative;
    z-index: 0;

    @include large-desktop {
        max-width: 1160px;
    }


    margin: 50px auto;
    color: $white;

    @include breakpoint(custom, 495) {
        margin: 20px auto;
    }


    .block__content {
        max-width: 1440px;
        position: relative;
        width: 100%;
        z-index: 1;

        @include large-desktop {
            max-width: 1160px;
        }


        width: 70%;

        @include breakpoint(custom, 768) {
            width: 100%;
        }


        .field-name-field {
            &-block-title {
                font-family: $font-title;
                font-weight: $medium;
                margin-bottom: 40px;

                @include font(40);

                @include breakpoint(custom, 495) {
                    @include font(30);
                }
            }

            &-text {
                @include font(15);

                margin-bottom: 60px;
            }
        }

        .field-type-link-field {
            float: left;
            margin-bottom: 15px;
            margin-right: 20px;

            a {
                @include button(rounded_white);

                display: inline-block;

                &:hover {
                    background-color: $white;
                    border-color: $white;
                }
            }
        }
    }
}

#block-bean-standard-page-hero-image {
    img {
        display: none;
    }
}

.field-name-field-image-hero {
    display: none;
}

