#block-views-7e2c62155b1d71d8de0378743eddbe46 {
    @include clearfix;

    background: $background2;
    padding: 30px 20px;

    .view-header {
        @include clearfix;
    }

    .block-title {
        @include font(32);

        float: left;
        padding: 0 0 30px;
        margin: 0 auto;
        margin-top: 4px;

        @include large-desktop {
            max-width: 1160px;
        }


        @include breakpoint(custom, 768) {
            text-align: center;
        }
    }

    .contribute-now {
        @include button(rounded_grey);

        float: right;
        margin-bottom: 20px;

        &:hover,
        &:focus {
            &:before {
                color: $orange1;
                right: 15px;
                opacity: 1;
            }

            &:after {
                color: $orange1;
                right: 0;
                opacity: 0;
            }
        }
    }

    .block__content {
        margin: 0 auto;

        @include large-desktop {
            max-width: 1160px;
        }


        .views-row {
            background: $white;
            padding: 20px;
            width: 48.9%;
            display: inline-block;
            margin: 0 20px 20px 0;
            float: left;
            position: relative;

            @include breakpoint(custom, 907) {
                width: 100%;
            }


            .content-image {
                width: 100%;
                padding-bottom: 20px;

                @include desktop {
                    float: left;
                    width: 40%;
                }


                img {
                    width: 100%;
                    height: auto;
                }

                & + div {
                    position: relative;
                }
            }

            .contribution-desc {
                background: $white;
                width: 60%;
                float: left;

                @include tablet {
                    padding: 0 0 0 20px;
                }

                @include breakpoint(custom, 768) {
                    width: 100%;
                }


                > div {
                    @include font(13);

                    margin-bottom: 20px;
                }

                .views-field-title {
                    @include font(24);

                    font-family: $font-title;
                    font-weight: $ultra-bold;
                    line-height: 1.25;

                    a {
                        @include transition(color, $animTime, $easeOutExpo);

                        &:hover,
                        &:focus {
                            color: $orange1;
                        }
                    }
                }

                .content-duration {
                    background: #e8e8e8;
                    border-radius: 20px;
                    margin-bottom: 30px;
                    min-height: 38px;
                    padding: 10px 20px !important;
                    width: 135px;

                    & > div {
                        &:before {
                            @extend %icomoon;

                            border-right: 1px solid #bfbfbf;
                            color: $orange1;
                            display: inline-block;
                            font-size: 20px;
                            height: 20px;
                            margin-right: 10px;
                            padding-right: 10px;
                            vertical-align: top;
                            width: 30px;
                        }
                    }

                    .field-item {
                        border-left: 1px solid #bfbfbf;
                        padding: 1px 0 1px 10px;
                        display: inline-block;
                        vertical-align: top;
                    }
                }

                .content-date-of-publication {
                    &:before {
                        @extend %icomoon;

                        content: "\e90b";
                        display: inline-block;
                        font-size: 14px;
                        height: 14px;
                        vertical-align: top;
                        margin-right: 10px;
                        position: relative;
                        top: 2px;
                        width: 14px;
                    }

                    & > div {
                        display: inline-block;
                        vertical-align: top;
                    }
                }

                .content-author {
                    margin-bottom: 20px;
                }

                .taxonomy-terms {
                    border-top: 1px solid #dedede;
                    padding-top: 20px;

                    a {
                        @include button(square_grey);

                        display: inline-block;
                    }
                }
            }

            &:first-of-type {
                width: 100%;

                .views-field-title {
                    @include font(36);
                }
            }

            &:last-of-type {
                margin-right: 0;
            }
        }
    }

    .video,
    .Video {
        .content-duration {
            & > div {
                &:before {
                    content: "\e90f";
                }
            }
        }
    }

    .podcast,
    .Podcast {
        .content-duration {
            & > div {
                &:before {
                    content: "\e90d";
                }
            }
        }
    }

    .article,
    .Article {
        .content-duration {
            & > div {
                &:before {
                    content: "\e90c";
                }
            }
        }
    }
}
